@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.3.0/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header__logo{background-position:center;background-size:contain;background-repeat:no-repeat;height:80px;min-width:150px;max-width:300px;background-image:url(data:image/png;base64,)}@media screen and (max-width: 470px){.header__logo{height:3.2rem}}@media screen and (max-width: 470px){.search-button{display:none}}@media screen and (max-width: 470px){.search-input{display:none}}
.device-item{height:100%;min-width:320px;max-width:1000px;background-color:#d8e1e8}.device-toogle{margin:5px;height:160px;max-width:1000px;min-width:300px}@media screen and (max-width: 350px){.device-toogle{height:130px}}.device-collapse-content{background-color:#e5f1fa;margin-right:5px;margin-left:5px}.pin{font-size:1.5rem}ul{flex:auto}li{text-align:start;font-size:1.2rem}i{text-align:start;font-size:.8rem;display:flex;overflow:hidden;text-overflow:ellipsis}@media screen and (max-width: 470px){.device-icon{display:none}}.device-name{font-size:2.2rem}@media screen and (max-width: 350px){.device-name{font-size:1.8rem}}.device-location{font-size:1rem}@media screen and (max-width: 350px){.device-location{font-size:.7rem}}.device-location-update{font-size:.8rem;padding-top:4px}@media screen and (max-width: 350px){.device-location-update{font-size:.6rem}}
.google-map{height:100vh;padding:5px}
.marker-none{color:red}.marker-blink{color:red;animation:blink 2s steps(5, start) infinite;-webkit-animation:blink 1s steps(5, start) infinite}@keyframes blink{to{visibility:hidden}}
.statistic-header{display:flex;flex-direction:row;justify-content:center}@media screen and (max-width: 470px){.statistic-header{display:block;padding:0 5px}}.statistic-item{background-color:#d8e1e8;color:#000;margin:5px;width:100%;min-width:100px;max-width:500px;height:100px}@media screen and (max-width: 470px){.statistic-item{height:4.2rem;margin:0px}}.statistic-title{padding-top:8px;font-size:17px;margin:0px}@media screen and (max-width: 470px){.statistic-title{padding-top:3px}}.statistic-counter{font-size:45px;padding-top:0px}@media screen and (max-width: 470px){.statistic-counter{font-size:1.8rem}}#alert-counter{background-color:#fd6363}#working-counter{background-color:#68f068}#total-counter{background-color:#3dc8ff}
.middle-content{height:94vh;min-width:300px;max-width:1500px;flex-grow:1;display:block}.right-content{min-width:200px;max-width:550px;width:100%}.main-contain{background-color:rgba(255,255,255,.247)}
.graphContainer{flex-grow:1;max-width:1000px;min-width:400px}.setThresholdContainer{margin-top:7vh;margin-left:50px;max-width:400px;min-width:200px}.selection-view{display:flex;justify-content:space-between;flex-direction:row}@media screen and (max-width: 1000px){.selection-view{display:block}}@media screen and (max-width: 1000px){.selection-form{width:90vw}}
.graph{height:90vh}@media screen and (max-width: 470px){.graph{height:70vh;width:95vw}}
.ant-slider-track{padding:5px}.ant-slider-rail{padding:5px}.ant-slider-handle{width:20px;height:20px;border-width:4px}.ant-slider-dot{width:14px;height:14px;border-width:3px}.ant-slider-mark-text{padding-left:10px}
